import { registerBlockType } from '@wordpress/blocks';
import {
	RichText,
	InspectorControls,
	useBlockProps,
} from '@wordpress/block-editor';

import {
	CheckboxControl,
	RadioControl,
	TextControl,
	ToggleControl,
	SelectControl,
	PanelBody,
} from '@wordpress/components';

registerBlockType('filter-plus/woo-filter', {
    title: 'WooCommerce Product Filter',
    icon: 'list',
    category: 'text',
	attributes: {
		content: {
			type: 'string',
			source: 'html',
			selector: 'p',
		},
		checkboxField: {
			type: 'boolean',
			default: true,
		},
		radioField: {
			type: 'string',
			default: 'yes',
		},
		textField: {
			type: 'string',
		},
		toggleField: {
			type: 'boolean',
		},
		selectField: {
			type: 'string',
		},
	},

	edit( { attributes, setAttributes } ) {
        const blockProps = useBlockProps();
        const {
            content,
            checkboxField,
            radioField,
            textField,
            toggleField,
            selectField,
        } = attributes;

        function onChangeContent( newContent ) {
            setAttributes( { content: newContent } );
        }

        function onChangeTextField( newValue ) {
            setAttributes( { textField: newValue } );
        }

        function onChangeToggleField( newValue ) {
            setAttributes( { toggleField: newValue } );
        }

        function onChangeSelectField( newValue ) {
            setAttributes( { selectField: newValue } );
        }
        return (
			<>
				<InspectorControls>
					<PanelBody title={ wp.i18n.__( 'Settings','filter-plus' ) }>

						<TextControl
							label="Text Field"
							help="Additional help text"
							value={ textField }
							onChange={ onChangeTextField }
						/>

						<ToggleControl
							label="Toggle Field"
							checked={ toggleField }
							onChange={ onChangeToggleField }
						/>

						<SelectControl
							label="Select Control"
							value={ selectField }
							options={ [
								{ value: 'a', label: 'Option A' },
								{ value: 'b', label: 'Option B' },
								{ value: 'c', label: 'Option C' },
							] }
							onChange={ onChangeSelectField }
						/>
					</PanelBody>
				</InspectorControls>
			</>
		);
	},

	save( { attributes } ) {
		const {
			content,
			checkboxField,
			radioField,
			textField,
			toggleField,
			selectField,
		} = attributes;
		const blockProps = useBlockProps.save();
        console.log(blockProps);
		return (
			<div { ...blockProps }>
				<h2>Inspector Control Fields</h2>
				<ul>
					<li>Checkbox Field: { checkboxField }</li>
					<li>Radio Field: { radioField }</li>
					<li>Text Field: { textField }</li>
					<li>Toggle Field: { toggleField }</li>
					<li>Select Field: { selectField }</li>
				</ul>
			</div>
		);
	},

} );
